import { Component } from '@angular/core';
import {AlertController, IonicPage,ModalController, NavController, NavParams, InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';



@IonicPage()
@Component({
  selector: 'page-marketmail-list',
  templateUrl: 'marketmail-list.html',
})
export class MarketmailListPage {

  public loading: boolean = true;
 public items: any=[];
  items2: any=[];
  tiposus: any=[];

    generated = '';
 public llave:any=[];
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  username: string = "";
  titulox:any;
  unidadt:any;
  categoria:any;
  idempresa:any;
  idafiliado:any='na';

//private barcodeScanner:BarcodeScanner,
  constructor( public alertCtrl: AlertController,public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService
    ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth
 

    ) {
      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value
       
    })
     
  }
  
  public clearSearchg(): void {
    this.gSearchQuery = '';
  }
  showFilter()     
  {


    
  }


  
  pideregistros() { 
    let self = this;
    self.loading = true;   
   
    if(this.navParams.data.idrol=='lluviaideas')
    {
      this.regService.getReg(this.navParams.data.idempresa +'/'+ this.categoria+'/categoria/'+this.username,'mailmarkets').then((data) => {
        this.items=data;
        self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
 
  });
    }
    else{
      this.regService.getReg(this.navParams.data.idempresa +'/'+ this.categoria+'/categoria/' , 'mailmarkets').then((data) => {
        this.items=data;
        self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
 
  });


    }
        
              
      }
      
  

      
  
  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }
  ionViewDidLoad() {
    this.categoria=this.navParams.data.idcategoria;
    /*
this.categoria=  this.navParams.get("nivel");

if(this.categoria)
{
  if(this.categoria=='')
  {
     this.categoria=this.navParams.data.idcategoria;
  }
    
}
*/


    this.titulox=this.navParams.data.titulo;
    this.storage.get('tiposus').then((value) => {
      this.tiposus = value.split(',');
    this.unidadt=this.tiposus[7]

    this.pideregistros();

  
  });


    
   
  }
  
  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }


  actdenuncia(op,item)     
  {


    let prompt = this.alertCtrl.create({
      title:'Actualizar ' + op,
      message: 'Ingrese nota:',
      inputs: [
        {
          name: 'nota'
        }
      ],
      buttons: [
        {
          text: 'Cancelar'
        },
        {
          text: 'Aceptar',
          handler: todo => {
 
                if(todo){
 
                  let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operación?',
    buttons: [
      {text: 'Si',
        handler: () => {
              
          let   options       : any;
      

           if(op=='Denuncia en proceso')
           {
            options       = { 
              estado : op,
              notamedio :todo.nota,
              bitacora:{idempresa:this.navParams.data.idempresa,idafiliado:'',email: this.username,permiso:'Actualiza',accion:' Actualiza denuncia '}
             };
           }
           else
           {
            options       = { 
              estado : op,
              notafin :todo.nota,
              bitacora:{idempresa:this.navParams.data.idempresa,idafiliado:'',email: this.username,permiso:'Actualiza',accion:' Actualiza denuncia '}
             };

           }
           
           this.regService.createReg(item._id,options,'mailmarkets').then((result) => {
            this.msj.showError3('Exito','Operación realizada con exito');


          }, (err) => {
         this.msj.showError3('Error',err.error);
        });
         
   
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();


                 
 
                }
 
 
          }
        }
      ]
    });
 
    prompt.present();


  }


  deleteRecord(item : any, no) : void
  {
    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operación?',
    buttons: [
      {text: 'Si',
        handler: () => {
              
      
    let recordID 		: string		= item._id;
    this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'mailmarkets').then((data) => {
        this.items.splice(no, 1);
        this.items = this.items.concat([]);
      }, (err) => {
        this.msj.showError3('Error',err.error);
    });
      
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();
     
  
  }
  value:any;

  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('MarketmailCreatePage', { record : item,titulo:this.titulox,idempresa:this.navParams.data.idempresa,idcategoria:this.categoria},{showBackdrop: true,enableBackdropDismiss: false,cssClass: "modal-fullscreen"});
    addModal.onDidDismiss(data => {
      if (data) {
        this.pideregistros()
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
   addRecord() : void
   {
    let addModal = this._MODAL .create('MarketmailCreatePage',{ record : null,titulo:this.titulox,idempresa:this.navParams.data.idempresa,idcategoria:this.categoria},{showBackdrop: true,enableBackdropDismiss: false,cssClass: "modal-fullscreen"});
    addModal.onDidDismiss(data => {
      if (data) {
        this.pideregistros()
      }  
    })
    addModal.present();

      //this.navCtrl.push('EmpresaCreatePage');
   }
 




}



